@import url('https://fonts.googleapis.com/css?family=Montserrat:600|Raleway:500,600,700');

.app {
  background-image: linear-gradient(to right,
    rgba(255, 255, 255, 0.75) ,
    rgba(255, 255, 255, 0.4)),
  url('contact.png');
  position: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-clip: cover;
  background-size: cover;
  background-attachment: fixed;
  overflow-x: hidden; 
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  display: flex;
  justify-content: flex-start;
  /* flex-direction: row; */
}

.scrollable {
  background: rgba(255, 255, 255, 0.9);
  height: -moz-fit-content;
  height: -webkit-fit-content;
  height: fit-content;
  position: absolute;
  top: 91vh;
  width: 650px;
  margin-left: 50%;
  margin-right: 50px;
  text-align: center;

  -webkit-transition: all .3s ease-in;
  -moz-transition: all .3s ease-in;
  -o-transition: all .3s ease-in;
  -ms-transition: all .3s ease-in;
  transition: all .3s ease-in;
}

.scrollable:hover {
  background: #ffffff;

  -webkit-transition: all .3s ease-in;
  -moz-transition: all .3s ease-in;
  -o-transition: all .3s ease-in;
  -ms-transition: all .3s ease-in;
  transition: all .3s ease-in;
}

.scrollable .chevron {
  margin-top: 20px;
  position: relative;
  animation: MoveUpDown 1.3s linear infinite;
}


@keyframes MoveUpDown {
  0%, 100% {
    top: 5px;
  }
  50% {
    top: 15px;
  }
}

.scrollable h1 {
  font-family: 'Montserrat', sans-serif;
  font-size: 2rem;
  font-weight: 600;
  color: #35a6de;
}

p {
  font-family: 'Raleway', sans-serif;;
  font-size: 1rem;
  font-weight: 500;
  color: #333333;
  text-align: left;
}

.scrollable .profile {
  display: flex;
  margin-left: 25px;
  margin-right: 25px;
  align-items: center;
}

.scrollable .profile img {
  width: 110px;
  height: 110px;
  margin-left: 15px;
  border-radius: 50%;  
}

.scrollable .profile .summary {
  font-family: 'Raleway', sans-serif;;
  font-size: 1rem;
  font-weight: 500;
  color: #333333;
  text-align: left;
  margin-left: 30px;
}

.scrollable .blog p {
  padding: 10px 0 0 0;
  text-align: center;
}

.scrollable .blog p a {
  color: #35a6de;
}

.scrollable h2 {
  font-family: 'Raleway', sans-serif;;
  font-size: 1rem;
  font-weight: 500;
  color: #333333;
  text-align: left;
  font-style: normal;
  margin-left: 25px;
  margin-bottom: 20px;
}

.scrollable em {
  font-family: 'Raleway', sans-serif;;
  font-size: 1rem;
  font-weight: 600;
  color: #35a6de;
  text-align: left;
  font-style: normal;
}

.scrollable .skills {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
  margin-left: 25px;
  margin-right: 25px;
}

.scrollable .skills em::before {
  content: "• ";
}

.scrollable .skills .left {
  margin-left: 15px;
  flex: 1;
}

.scrollable .skills .list {
  flex: 2.5;
}

.scrollable .skills .list .languages em {
  font-weight: 800;
}

.scrollable .skills .list .frameworks em {
  font-weight: 600;
}

.scrollable .skills .list .software em {
  font-weight: 400;
}

@keyframes strikein {
  0%     { transform-origin:  0% 50%;transform:scaleX(0); }
  50%    { transform-origin:  0% 50%;transform:scaleX(1); }
}
@keyframes strikeout {
  50%    { transform-origin:  0% 50%;transform:scaleX(1); }
  0%     { transform-origin:  0% 50%;transform:scaleX(0); }
}

.strike {
  position: relative;
  animation: strikein .75s ease-in-out forwards;
}
.strike:hover:after {
  content: ' ';
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 3px;
  background: #35a6de;
  animation: strikein .75s ease-in-out forwards;
}

@media screen and (max-width: 600px) {

  .app {
    justify-content: center;
    background-image: url('contact2.png');

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    overflow-y: scroll;
    overflow-x: hidden;
    text-align: center;
    align-content: center;

    background-position-y: 0;
    background-repeat: no-repeat;
    background-clip: border-box;
    background-size: auto 350px;
  }
  
  .scrollable {
    width: 100%;
    margin-top: 0;
    height: fit-content;
    margin-left: 0;
    margin-right: 0;
    opacity: 1;
    position: static;
    top: 0;
    background-color: #ffffff;
  }

  .scrollable .chevron {
    display: none;
  }

  .scrollable .profile .summary {
    font-size: 0.95rem;
  }

  .scrollable .skills .list {
    flex: 2;
  }
}

@media screen and (max-width: 485px){

  .scrollable .profile {
    margin-left: 0px;
    margin-right: 15px;
  }

  .scrollable .profile img {
    margin-left: 15px;
  }

  .scrollable .blog p {
    font-size: 0.95rem;
    margin: 0 10px;
    padding: 20px 0 0 0;
  }

  .scrollable .skills {
    margin-left: 0px;
  }
  
  .scrollable .skills .left {
    margin-right: 15px;
  }
  .scrollable .skills p {
    font-size: 0.95rem;
  }

  .scrollable .skills .list em {
    font-size: 0.90rem;
  }
}

@media screen and (max-width: 430px){

  .scrollable .profile .summary {
    font-size: 0.95rem;
  }

  .scrollable .blog p {
    font-size: 0.9rem;
    margin: 0 15px;
  }

  .scrollable .skills .left {
    font-size: 0.9rem;
    flex: 1.5;
    margin-right: 5px;
  }

  .scrollable .skills .list span em {
    font-size: 0.9rem;
    flex: 1;
  }
  
}

@media screen and (max-width: 375px){

  .scrollable h1 {
    font-size: 1.5rem;
  }

  .scrollable .skills {
    margin-right: 10px;
  }
  
}

@media screen and (max-height: 800px){

  .scrollable {
    top: 80vh;
  }
  
}

@media screen and (max-height: 1100px){

  .scrollable {
    top: 94vh;
  }
  
}

